import firebase from "./Config"
import { ref, set, get , child, update} from "firebase/database";

const database = ref(firebase, 'usuarios/')

export default class usuario {
      
      public progresso = {
        1 : {
          1: 0,
          2: 0,
          3: 0
        },
        2 : {
            1: 0,
            2: 0,
            3: 0
        },
        3 : {
            1: 0,
            2: 0,
            3: 0
        },
        4 : {
            1: 0,
            2: 0,
            3: 0
        },
        5 : {
            1: 0,
            2: 0,
            3: 0
        }
      }

      
      public rank = {
          pontuacao: 0,
          tempo: 0
      }

      constructor(
         public nick: string 
         
      ) {  
        if(nick){
          console.log("verifique");
          let teste = this
          console.log("Antes",teste);
          this.checkNick(nick)
          
        }else{
          console.log("Erro");
          
        }



      }

      setNickname(nick: string){
        this.nick = nick
      }
  
      public setProgresso(modo:  1 | 2 | 3 | 4 | 5, fase:  1 | 2 | 3, estrelas: any){        
        this.progresso[modo][fase] = estrelas
        this.updateUsuario()
      }

      setPontuacao(pontos: number){
        this.rank.pontuacao = this.rank.pontuacao + pontos
        this.updateUsuario()
      }

      checkNick(nick: string){
        get(child(database,`/${nick}`)).then((snapshot) => {
          if (snapshot.exists()) {
            console.log("SUCESSO!");
            
            this.loadUsuario(snapshot.val())
            console.log("Depois",this);


          } else {
            console.log("VAzio");
            
            this.create(nick)
          }
        }).catch((error) => {
          console.log("Erro");
          
          console.error(error);
        });
      }

      create(nick: string) {
          this.setNickname(nick)
          set(ref(firebase, "usuarios/"+this.nick), this);
      }

      updateUsuario() {
        update(ref(firebase, "usuarios/"+this.nick), this);
      }

      loadUsuario(usuario: usuario){

        
          this.nick = usuario.nick
          this.progresso = usuario.progresso
          this.rank = usuario.rank


      }
    
}
